/* TAILWIND */
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  /* RESET */
  *, *:before, *:after { @apply box-border !outline-none; }
  html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video { @apply m-0 p-0 border-none text-[100%] [font:inherit] align-baseline [text-rendering:optimizeLegibility] [-webkit-font-smoothing:antialiased] [text-size-adjust:none]; }
  body { @apply text-[100%] leading-normal font-sans select-none bg-bodyBackgroundColor; }
  input, form, table, select, textarea, button { @apply font-sans text-[100%] leading-normal rounded-none !appearance-none resize-none outline-none border-none bg-transparent; }
  select::-ms-expand { @apply !hidden; }
  *, *:focus, *:focus-visible, *:hover, *:active, *::before, *::after { @apply !outline-none; }
  footer, header, nav, section, main, article { @apply block; }
  a, a:hover, a:active, a:focus, a:visited { @apply decoration-0; }
  ol, ul, menu { @apply list-none m-0 p-0; }    
  blockquote, q { @apply [quotes:none]; }
  blockquote:before, blockquote:after, q:before, q:after { @apply content-none; }
  table { @apply border-spacing-0 border-collapse; }
  input { @apply appearance-none; }
  svg { @apply overflow-visible block; }
  input[type=search]::-ms-clear { @apply hidden h-0 w-0; }
  input[type=search]::-ms-reveal { @apply hidden h-0 w-0; }
  input[type="search"]::-webkit-search-decoration,
  input[type="search"]::-webkit-search-cancel-button,
  input[type="search"]::-webkit-search-results-button,
  input[type="search"]::-webkit-search-results-decoration { @apply hidden; }
  strong {@apply !font-bold;}
  
  /* TABLE FIX */
  .table-wrapper {@apply !overflow-x-auto !overflow-y-hidden;}
  .table-wrapper {@apply scrollbar scrollbar-h-[5px] scrollbar-track-rounded-[10px] scrollbar-thumb-rounded-[10px] scrollbar-thumb-primary-500 scrollbar-track-primary-500/10;}
  .table-wrapper table {@apply !m-0;}
  .table-wrapper table {@apply text-[#111] w-full min-w-[768px] border-spacing-0 border-collapse;}
  .table-wrapper table tr {@apply border border-solid border-[#111]/50;}
  .table-wrapper table tr td {@apply border border-solid border-[#111]/50 py-[10px] px-[50px];}

  /* FORM | SELECT FIX */
  select:required:invalid { @apply text-black/50; }
  option[value=""][disabled] { @apply hidden; }
  option {@apply text-black;}

  /* MAIN BASED */
  body {@apply scrollbar scrollbar-w-[5px] scrollbar-track-rounded-[5px] scrollbar-thumb-rounded-[5px] scrollbar-thumb-primary-500 scrollbar-track-primary-500/10}
}

.career-content .text-editor {
    :is(h1,h2,h4,h5,h6) {
        @apply text-[40px] 2xl:text-[35px] lg:text-[24px] leading-[65px] 2xl:leading-[50px] lg:leading-[38px] font-bold text-secondary-main tracking-[-0.4px] mb-[30px];
    }

    h3 {
        @apply text-[30px] 2xl:text-[25px] lg:text-[20px] leading-[38px] lg:leading-[30px] font-medium text-paragraph tracking-[-0.3px] mb-[20px];
    }

    p {
        @apply text-[18px] lg:text-[16px] font-light leading-[32px] tracking-[-0.18px] text-paragraph;
    }

    ul {
        @apply mb-[90px] 2xl:mb-[75px] xl:mb-[55px] lg:mb-[40px] md:mb-[20px] flex flex-col gap-[20px];

        & li {
            @apply text-[17px] lg:text-[16px] font-light leading-[32px] tracking-[-0.17px] text-paragraph flex items-center;

            &:before {
                @apply content-[''] mr-[18px] block min-w-[12px] aspect-square bg-primary-main;
            }
        }
    }
}

.news-detail .text-editor {
    :is(h1,h2,h4,h5,h6) {
        @apply text-[30px] 2xl:text-[25px] lg:text-[22px] leading-[45px] lg:leading-[38px] font-bold text-secondary-main mb-[30px];
    }

    p {
        @apply text-[18px] lg:text-[16px] font-light leading-[32px] tracking-[-0.18px] text-paragraph mb-[30px];
    }

    ul {
        @apply mb-[30px] flex flex-col gap-[20px];

        & li {
            @apply text-[17px] lg:text-[16px] font-light leading-[32px] tracking-[-0.17px] text-paragraph flex items-center;

            &:before {
                @apply content-[''] mr-[18px] block min-w-[12px] aspect-square bg-primary-main;
            }
        }
    }
}


.offer-fancybox {
    .fancybox__backdrop {
        @apply bg-[#f6f6f6]/15 backdrop-blur-[5px];;
    }

    .f-button.is-close-btn {
        @apply hidden;
    }

    .fancybox__slide.has-inline {
        @apply !p-0 overflow-hidden;
    }
}

.popup .text-editor {
    & p, & li {
        @apply text-[18px] md:text-[16px] xs:text-[14px] text-paragraph font-light mb-[20px];
    }

    :is(h1,h2,h3,h4,h5,h6) {
        @apply text-[32px] lg:text-[28px] leading-[32px] md:text-[22px] md:leading-[30px] tracking-[-0.32px] text-secondary-main font-medium mb-[20px];
    }
}

.stars {
    display: flex;
    gap: 10px;
}

.stars .rating-star {
    cursor: pointer;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.stars .rating-star svg {
    width: 20px;
    height: 20px;
    fill: #E1E1E1;
    transition: 0.45s;
}

.stars .rating-star.to-rate svg {
    fill: #FFD600;
}

.stars .rating-star.rated svg {
    fill: #FFD600;
}

.stars .rating-star.no-to-rated svg {
    fill: #E1E1E1;
}

.stars .rating-star.to-hover svg {
    fill: #FFD600;
}

